<template>
  <el-dialog
    :title="formTitle"
    :visible="dialogVisible"
    :show-close="false"
    width="45%"
  >
    <el-form
      :model="ruleForm"
      :rules="rules"
      ref="ruleForm"
      label-width="100px"
      class="demo-ruleForm"
    >
      <el-form-item label="用户名" prop="username">
        <el-input v-model="ruleForm.username"></el-input>
      </el-form-item>
      <el-form-item label="密码" prop="password">
        <el-input type="password" v-model="ruleForm.password"></el-input>
      </el-form-item>
      <el-form-item label="昵称" prop="name">
        <el-input v-model="ruleForm.name"></el-input>
      </el-form-item>
      <el-form-item label="性别" prop="sex">
        <el-select v-model="ruleForm.sex" value="">
          <el-option label="男" value="男"></el-option>
          <el-option label="女" value="女"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="出生日期" prop="birthday">
        <el-col :span="20">
          <el-form-item prop="date">
            <el-date-picker
              type="date"
              format="yyyy-MM-dd"
              placeholder="选择日期"
              v-model="ruleForm.birthday"
              style="width: 100%"
            ></el-date-picker>
          </el-form-item>
        </el-col>
      </el-form-item>
      <el-form-item label="邮箱" prop="mailBox">
        <el-input v-model="ruleForm.mailBox"></el-input>
      </el-form-item>
      <el-form-item label="手机号码" prop="phone">
        <el-input v-model="ruleForm.phone"></el-input>
      </el-form-item>
      <el-form-item label="地址" prop="address">
        <el-input v-model="ruleForm.address"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submitForm('ruleForm')"
          >确 定</el-button
        >
        <el-button @click="resetForm('ruleForm')">重 置</el-button>
        <el-button @click="closeForm()">取 消</el-button>
      </el-form-item>
    </el-form>
  </el-dialog>
</template>

<script>
import myAxios from "@/request/myAxios";
import pubsub from "pubsub-js";
import { mapState, mapMutations } from "vuex";

export default {
  name: "AddDialog",
  data() {
    return {
      id: "",
      ruleForm: {
        username: "",
        password: "",
        authority: "",
        name: "",
        sex: "",
        birthday: "",
        mailBox: "",
        phone: "",
        address: "",
      },
      rules: {
        username: [
          { required: true, message: "请输入用户名", trigger: "blur" },
          {
            min: 6,
            max: 18,
            message: "长度在 6 到 18 个字符",
            trigger: "blur",
          },
        ],
        password: [
          { required: true, message: "请输入密码", trigger: "blur" },
          {
            min: 8,
            max: 18,
            message: "长度在 8 到 18 个字符",
            trigger: "blur",
          },
        ],
        name: [
          { required: true, message: "请输入昵称", trigger: "blur" },
          {
            min: 2,
            max: 18,
            message: "长度在 2 到 16 个字符",
            trigger: "blur",
          },
        ],
        sex: [{ required: true, message: "请选择性别", trigger: "blur" }],
        birthday: [
          {
            required: true,
            message: "请选择日期",
            trigger: "blur",
          },
        ],
        mailBox: [{ required: true, message: "请输入邮箱", trigger: "blur" }],
        phone: [
          { required: true, message: "请输入手机号", trigger: "blur" },
          { min: 11, max: 11, message: "请输入正确的手机号", trigger: "blur" },
        ],
      },
    };
  },
  computed: {
    ...mapState("mySystem", ["dialogVisible", "formTitle"]),
  },
  mounted() {
    this.pubId = pubsub.subscribe("setForm", (__, id) => {
      console.log("回填表单开始，id为", id);
      this.setForm(id);
    });
  },
  beforeDestroy() {
    pubsub.unsubscribe(this.pubId);
  },
  methods: {
    ...mapMutations("mySystem", ["CHANGE_DV", "CHANGE_FT"]),

    /*清空对话框数据*/
    clearForm() {
      this.ruleForm = {
        username: "",
        password: "",
        name: "",
        sex: "",
        birthday: "",
        mailBox: "",
        phone: "",
        address: "",
      };
    },

    /*更新表格时需要回调数据*/
    setForm(id) {
      this.id = id;
      myAxios({
        method: "get",
        url: `/api/users/${id}`,
      }).then((resp) => {
        console.log(resp.data);
        if (resp.data.code == "20041") {
          this.ruleForm = resp.data.data;
          this.CHANGE_FT("修改用户");
          this.CHANGE_DV();
        } else {
          this.$message({
            message: `${resp.data.msg}`,
            type: "error",
          });
        }
      });
    },

    /*提交表单*/
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          console.log(this.ruleForm);
          console.log(this.formTitle);
          /*判断是新增用户还是更新用户*/
          if (this.formTitle == "新增用户") {
            console.log("新增");
            myAxios({
              method: "post",
              url: "/api/users/insert",
              data: this.ruleForm,
            }).then((resp) => {
              console.log(resp.data);
              if (resp.data.code == "20011") {
                this.CHANGE_DV(); //关闭对话框
                this.clearForm(); //清空对话框数据
                /*刷新数据*/
                pubsub.publish("updateTable");
                this.$message({
                  message: "新增成功",
                  type: "success",
                });
              } else {
                this.$message({
                  message: `${resp.data.msg}`,
                  type: "error",
                });
              }
            });
          } else {
            console.log("修改");
            myAxios({
              method: "put",
              url: `/api/users/update`,
              data: this.ruleForm,
            }).then((resp) => {
              console.log(resp.data);
              if (resp.data.code == "20031") {
                this.CHANGE_DV(); //关闭对话框
                this.clearForm(); //清空对话框数据
                pubsub.publish("updateTable");
                this.$message({
                  message: "更新成功",
                  type: "success",
                });
                /*刷新数据*/
              } else {
                this.$message({
                  message: `${resp.data.msg}`,
                  type: "error",
                });
              }
            });
          }
        }
      });
    },

    /*点击取消，关闭表单和对话框*/
    closeForm() {
      this.CHANGE_DV(); //关闭对话框
      this.CHANGE_FT("新增用户"); //修改对话框标题
      this.clearForm(); //清空对话框数据
    },

    /*清空表格*/
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
  },
};
</script>

