<template>
  <el-container>
    <el-header class="header">
      <span>账户信息管理</span>
    </el-header>
    <el-main>
      <el-form :inline="true" :model="formInline" class="demo-form-inline">
        <el-form-item label="查询：">
          <el-input
            v-model="formInline.selectUser"
            placeholder="请输入用户名"
            clearable
          ></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="selectButton()">查询</el-button>
          <el-button type="success" @click="CHANGE_DV()">新增</el-button>
        </el-form-item>
      </el-form>
      <template>
        <el-table
          ref="multipleTable"
          :data="tableData"
          tooltip-effect="dark"
          stripe
          border
          style="width: 100%"
          @selection-change="handleSelectionChange"
        >
          <el-table-column type="selection" width="50"> </el-table-column>
          <el-table-column label="序号" type="index" width="50">
          </el-table-column>
          <el-table-column prop="name" label="昵称" width="90">
          </el-table-column>
          <el-table-column prop="username" label="用户名" width="120">
          </el-table-column>
          <el-table-column prop="sex" label="性别" width="60">
          </el-table-column>
          <el-table-column label="出生日期" width="120">
            <template slot-scope="scope">{{ scope.row.birthday }}</template>
          </el-table-column>
          <el-table-column prop="phone" label="手机号" width="140">
          </el-table-column>
          <el-table-column prop="mailBox" label="邮箱" width="220">
          </el-table-column>
          <el-table-column prop="address" label="地址" show-overflow-tooltip>
          </el-table-column>
          <el-table-column label="操作" width="120">
            <template slot-scope="scope">
              <el-button
                type="primary"
                icon="el-icon-edit"
                circle
                @click="handleEdit(scope.$index, scope.row)"
              ></el-button>
              <el-button
                type="danger"
                icon="el-icon-delete"
                circle
                @click="handleDelete(scope.$index, scope.row)"
              ></el-button>
            </template>
          </el-table-column>
        </el-table>
        <div class="block">
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="currentPage"
            :page-sizes="[5, 10, 15, 20]"
            :page-size="pageSize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="recordCount"
          >
          </el-pagination>
        </div>
        <div style="margin-top: 20px">
          <el-button type="info" @click="toggleSelection()">
            取消选择
          </el-button>
          <el-button type="danger" @click="deleteSelection()">
            批量删除
          </el-button>
        </div>
      </template>
    </el-main>
  </el-container>
</template>

<script>
import myAxios from "@/request/myAxios";
import pubsub from "pubsub-js";
import { mapState, mapMutations } from "vuex";

export default {
  name: "AccountInfoTable",
  data() {
    return {
      formInline: {
        selectUser: "",
      },
      pageSize: 5,
      currentPage: 1,
      multipleSelection: [],
    };
  },
  computed: {
    ...mapState("mySystem", [
      "dialogVisible",
      "formTitle",
      "tableData",
      "recordCount",
      // "pageSize",
      // "currentPage",
    ]),
  },
  mounted() {
    this.select();
    //订阅事件，新增数据时更新表
    this.pubId = pubsub.subscribe("updateTable", () => {
      this.select();
    });
  },
  beforeDestroy() {
    pubsub.unsubscribe(this.pubId);
  },
  methods: {
    ...mapMutations("mySystem", [
      "CHANGE_DV",
      "CHANGE_FT",
      "CHANGE_TABLEDATA",
      "CHANGE_RECORDCOUNT",
      // "CHANGE_PAGESIZE",
      // "CHANGE_CURRENTPAGE",
    ]),
    selectButton() {
      this.currentPage = 1;
      this.select();
    },

    select() {
      let username = this.formInline.selectUser;
      console.log("username:", username);
      let url = `/api/users/info/limit?currentPage=${this.currentPage}&pageSize=${this.pageSize}`;
      if (username !== "" && username !== null) url += `&username=${username}`;
      console.log(
        "这是查询功能，用于查询修改表格数据",
        url,
        this.currentPage,
        this.pageSize,
        username
      );
      myAxios({
        method: "get",
        url: url,
      }).then((resp) => {
        console.log(resp.data.data);
        this.CHANGE_TABLEDATA(resp.data.data);
      });
      url = `/api/users`;
      if (username !== "" && username !== null) url += `?username=${username}`;
      myAxios({
        method: "get",
        url: url,
      }).then((resp) => {
        console.log(resp.data.data);
        this.CHANGE_RECORDCOUNT(resp.data.data);
      });
    },

    /*批量删除*/
    deleteSelection() {
      console.log("这是批量删除", this.multipleSelection);
      let ids = [];
      for (const key in this.multipleSelection) {
        if (Object.hasOwnProperty.call(this.multipleSelection, key)) {
          const element = this.multipleSelection[key];
          ids.push(element.id);
        }
      }
      console.log(ids);
      //提示确定，提交删除
      if (
        ids.length ===
          this.recordCount - (this.currentPage - 1) * this.pageSize ||
        ids.length === this.pageSize
      )
        this.currentPage -= 1;
      this.deleteConfirms(ids);
    },

    /*批量删除确定*/
    deleteConfirms(ids) {
      this.$confirm("此操作将永久删除选中用户, 是否继续?", "警告", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          myAxios({
            method: "delete",
            url: `/api/users/delete/`,
            data: ids,
          }).then((resp) => {
            if (resp.data.code == "20021") {
              this.$message({
                message: "删除成功",
                type: "success",
              });
              //重新查询
              this.select();
            } else {
              this.$message({
                message: `${resp.data.msg}`,
                type: "error",
              });
            }
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除",
          });
        });
    },

    /*表格单项修改*/
    handleEdit(index, row) {
      console.log("这是表格单项修改", index, row);
      //修改对话框标题和显示属性
      pubsub.publish("setForm", row.id);
      //提交修改，重新查询
    },

    /*表格单项删除*/
    handleDelete(index, row) {
      console.log("这是表格单项删除", index, row);
      //提示确定，提交删除
      if (index === 0) this.currentPage -= 1;
      this.deleteConfirm(row.id);
    },
    /*单项删除确定*/
    deleteConfirm(id) {
      this.$confirm("此操作将永久删除选中用户, 是否继续?", "警告", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          myAxios({
            method: "delete",
            url: `/api/users/delete/${id}`,
          }).then((resp) => {
            console.log(resp.data);
            if (resp.data.code == "20021") {
              this.$message({
                message: "删除成功",
                type: "success",
              });
              //重新查询
              this.select();
            } else {
              this.$message({
                message: `${resp.data.msg}`,
                type: "error",
              });
            }
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除",
          });
        });
    },

    /*表格取消选择*/
    toggleSelection(rows) {
      if (rows) {
        rows.forEach((row) => {
          this.$refs.multipleTable.toggleRowSelection(row);
        });
      } else {
        this.$refs.multipleTable.clearSelection();
      }
    },

    /*表格选择回调*/
    handleSelectionChange(val) {
      console.log("这是表格选项修改", val);
      this.multipleSelection = val;
    },

    /*分页条*/
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
      this.pageSize = val;
      // this.CHANGE_PAGESIZE(val);
      console.log("更改每页条数，这里得查询，并传递参数");
      this.select();
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
      this.currentPage = val;
      // this.CHANGE_CURRENTPAGE(val);
      console.log("更改当前页，这里得查询，并传递参数");
      this.select();
    },
  },
};
</script>

<style scoped>
.header {
  text-align: left;
  font-size: 24px;
  line-height: 40px;
}
</style>