<template>
  <el-container>
    <el-header class="header">
      <span>账户信息管理</span>
    </el-header>
    <el-main>
      <el-form :inline="true" :model="formInline" class="demo-form-inline">
        <el-form-item label="查询：">
          <el-input
            v-model="formInline.selectUser"
            placeholder="请输入用户名"
            clearable
          ></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="selectButton()">查询</el-button>
        </el-form-item>
      </el-form>
      <template>
        <el-table
          ref="multipleTable"
          :data="tableDataChara"
          tooltip-effect="dark"
          stripe
          border
          style="width: 100%"
        >
          <el-table-column label="序号" type="index" width="50">
          </el-table-column>
          <el-table-column prop="name" label="昵称" width="140">
          </el-table-column>
          <el-table-column prop="username" label="用户名" width="140">
          </el-table-column>
          <el-table-column prop="authority" label="权限" width="140">
          </el-table-column>
          <el-table-column label="操作" width="80">
            <template slot-scope="scope">
              <el-button
                type="primary"
                icon="el-icon-edit"
                circle
                @click="handleEdit(scope.$index, scope.row)"
              ></el-button>
            </template>
          </el-table-column>
        </el-table>
        <div class="block">
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="currentPage"
            :page-sizes="[5, 10, 15, 20]"
            :page-size="pageSize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="recordCountChara"
          >
          </el-pagination>
        </div>
      </template>
    </el-main>
  </el-container>
</template>

<script>
import myAxios from "@/request/myAxios";
import pubsub from "pubsub-js";
import { mapState, mapMutations } from "vuex";

const authorityName = ["超级管理员", "管理员", "普通用户"];
export default {
  name: "AccountCharaTable",
  data() {
    return {
      currentPage: 1,
      pageSize: 5,

      formInline: {
        selectUser: "",
      },
    };
  },
  computed: {
    ...mapState("mySystem", [
      "dialogVisibleChara",
      "recordCountChara",
      "tableDataChara",
    ]),
  },
  mounted() {
    this.select();
    //订阅事件，新增数据时更新表
    this.pubId = pubsub.subscribe("updateCharaTable", () => {
      this.select();
    });
  },
  beforeDestroy() {
    pubsub.unsubscribe("updateCharaTable");
  },
  methods: {
    ...mapMutations("mySystem", [
      "CHANGE_DV_CHARA",
      "CHANGE_TABLEDATA_CHARA",
      "CHANGE_RECORDCOUNT_CHARA",
    ]),

    selectButton() {
      this.currentPage = 1;
      this.select();
    },

    select() {
      let username = this.formInline.selectUser;
      console.log("username:", username);
      let url = `/api/users/chara/limit?currentPage=${this.currentPage}&pageSize=${this.pageSize}`;
      if (username !== "" && username !== null) url += `&username=${username}`;
      console.log(
        "这是查询功能，用于查询修改表格数据",
        url,
        this.currentPage,
        this.pageSize,
        username
      );
      myAxios({
        method: "get",
        url: url,
      }).then((resp) => {
        console.log(resp.data.data);
        for (const key in resp.data.data) {
          if (Object.hasOwnProperty.call(resp.data.data, key)) {
            const element = resp.data.data[key];
            element.authority = authorityName[element.authority];
          }
        }
        this.CHANGE_TABLEDATA_CHARA(resp.data.data);
      });
      url = `http://localhost/api/users`;
      if (username !== "" && username !== null) url += `?username=${username}`;
      myAxios({
        method: "get",
        url: url,
      }).then((resp) => {
        console.log(resp.data.data);
        this.CHANGE_RECORDCOUNT_CHARA(resp.data.data);
      });
    },

    /*表格单项修改*/
    handleEdit(index, row) {
      console.log("这是表格单项修改", index, row);
      //提交修改，重新查询
      pubsub.publish("setCharaForm", row.id);
    },

    /*分页条*/
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
      this.pageSize = val;
      console.log("更改每页条数，这里得查询，并传递参数");
      this.select();
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
      this.currentPage = val;
      console.log("更改当前页，这里得查询，并传递参数");
      this.select();
    },
  },
};
</script>

<style scoped>
.header {
  text-align: left;
  font-size: 24px;
  line-height: 40px;
}
</style>