// 创建Vuex中的store对象
import Vue from 'vue'
import Vuex from 'vuex'
//import axios from 'axios'

Vue.use(Vuex)

const mySystem = {
  namespaced: true,
  actions: {
  },
  mutations: {

    CHANGE_TOKEN(state, value) {
      state.token = value;
    },

    //修改表格数据
    CHANGE_TABLEDATA(state, value) {
      state.tableData = value;
    },
    //修改表格记录数
    CHANGE_RECORDCOUNT(state, value) {
      state.recordCount = value;
    },
    // //修改每页条数
    // CHANGE_PAGESIZE(state, value) {
    //   state.pageSize = value;
    // },
    // //修改当前页号
    // CHANGE_CURRENTPAGE(state, value) {
    //   state.currentPage = value;
    // },

    //修改表格数据
    CHANGE_TABLEDATA_CHARA(state, value) {
      state.tableDataChara = value;
    },
    //修改表格记录数
    CHANGE_RECORDCOUNT_CHARA(state, value) {
      state.recordCountChara = value;
    },

    //修改表单对话框可见
    CHANGE_DV(state) {
      state.dialogVisible = !state.dialogVisible
    },
    //修改表单对话框名字
    CHANGE_FT(state, value) {
      state.formTitle = value
    },

    //修改表单对话框可见
    CHANGE_DV_CHARA(state) {
      state.dialogVisibleChara = !state.dialogVisibleChara
    },
  },
  state: {
    //表单对话框可见
    dialogVisible: false,
    //表单对话框名
    formTitle: "新增用户",

    //得到的记录总数
    recordCount: 10,
    // pageSize: 5,
    // currentPage: 1,
    //账户信息表
    tableData: [],

    //权限表单对话框可见
    dialogVisibleChara: false,

    //权限表得到的记录总数
    recordCountChara: 1,
    //用户权限表
    tableDataChara: [],

  },
}


export default new Vuex.Store({
  modules: {
    mySystem,
  }
})