<template>
  <div id="loginDiv">
    <form id="form">
      <h1 id="loginMsg">LOGIN IN</h1>
      <em id="errorMsg"></em>
      <p>
        Username:
        <el-input
          placeholder="请输入用户名"
          v-model="loginTable.username"
        ></el-input>
      </p>
      <p>
        Password:
        <el-input
          placeholder="请输入密码"
          v-model="loginTable.password"
          show-password
        ></el-input>
      </p>
      <p class="pbtn">
        <el-button
          :loading="loading"
          ref="loginButton"
          type="primary"
          round
          @click="login()"
        >
          登 录
        </el-button>
      </p>
    </form>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "LoginC",
  data() {
    return {
      loginTable: {
        username: "",
        password: "",
      },
      loading: false,
    };
  },
  mounted() {},
  methods: {
    //登录
    login() {
      this.loading = true;
      axios({
        method: "post",
        url: `http://localhost/api/users/login`,
        data: this.loginTable,
      }).then((resp) => {
        console.log(resp.data);
        this.$refs.loginButton.$loading = false;
        if (resp.data.code == "20001") {
          //存储欢迎词
          localStorage.setItem("name", resp.data.data.name);
          //存储Token
          localStorage.setItem("token", resp.data.data.token);
          //路由跳转
          this.$router.replace({
            name: "MainV",
          });
        } else {
          this.$message({
            message: `${resp.data.msg}`,
            type: "error",
          });
        }
      });
      this.loading = false;
    },
  },
};
</script>

<style scoped>
#loginDiv {
  width: 40%;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 360px;
  background-color: rgba(75, 81, 95, 0.9);
  box-shadow: 7px 7px 17px rgba(52, 56, 66, 0.7);
  border-radius: 5px;
}

p {
  font-size: 20px;
  margin-top: 20px;
  color: rgb(72, 170, 255);
}

.pbtn {
  display: flex;
  justify-content: center;
  align-items: center;
}

#loginMsg {
  text-align: center;
  color: rgb(72, 170, 255);
}
</style>
