import VueRouter from 'vue-router'
import myAxios from '@/request/myAxios'

import LoginV from "@/views/login/LoginV"
import MainV from "@/views/main/MainV"
import AccountInfomation from '@/views/main/AccountInfomation'
import AccountCharacter from '@/views/main/AccountCharacter'
import BookInformation from '@/views/main/BookInformation'
import BookManagement from '@/views/main/BookManagement'
import MenuManagement from '@/views/main/MenuManagement'

const myRouter = new VueRouter({
  routes: [
    {
      path: '/',
      redirect: '/login'
    },
    {
      name: "LoginV",
      path: '/login',
      component: LoginV,
    },
    {
      name: "MainV",
      path: '/main',
      component: MainV,
      redirect: '/main/acinfo',
      children: [
        {
          name: "AccountInfomation",
          path: 'acinfo',
          component: AccountInfomation,
        },
        {
          name: "AccountCharacter",
          path: 'acchara',
          component: AccountCharacter,
        },
        {
          name: "BookInformation",
          path: 'bookinfo',
          component: BookInformation,
        },
        {
          name: "BookManagement",
          path: 'bookman',
          component: BookManagement,
        },
        {
          name: "MenuManagement",
          path: 'menuman',
          component: MenuManagement,
        },
      ]
    },
  ],
});

myRouter.beforeEach((to, from, next) => {
  //console.log(to, from, next);
  let token = localStorage.getItem('token')
  if (to.path === '/login') {
    if (token) {
      //console.log(token)
      //验证token正确性
      myAxios({
        method: "get",
        url: `/api/users/tokenCheck`,
      }).then((resp) => {
        console.log(resp.data);
        if (resp.data.code == "20001") {
          //设置欢迎词
          window.localStorage.setItem("name", resp.data.data)
          //跳转页面
          next({ path: '/main/acinfo' })
        } else {
          this.$message({
            message: `${resp.data.msg}`,
            type: "error",
          });
        }
      });

    } else {
      next()
    }
  } else {
    if (token) {
      next()
    } else {
      next({ path: '/login' })
    }
  }
})

export default myRouter