<template>
  <el-dialog
    :title="formTitleChara"
    :visible="dialogVisibleChara"
    :show-close="false"
    width="45%"
  >
    <el-form
      :model="ruleForm"
      :rules="rules"
      ref="ruleForm"
      label-width="100px"
      class="demo-ruleForm"
    >
      <el-form-item label="昵称" prop="name">
        {{ ruleForm.name }}
      </el-form-item>
      <el-form-item label="用户名" prop="username">
        {{ ruleForm.username }}
      </el-form-item>
      <el-form-item label="权限" prop="authority">
        <el-select v-model="ruleForm.authority" value="">
          <el-option label="超级管理员" value="超级管理员"></el-option>
          <el-option label="管理员" value="管理员"></el-option>
          <el-option label="普通用户" value="普通用户"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submitForm('ruleForm')">
          确 定
        </el-button>
        <el-button @click="closeForm()">取 消</el-button>
      </el-form-item>
    </el-form>
  </el-dialog>
</template>

<script>
import myAxios from "@/request/myAxios";
import pubsub from "pubsub-js";
import { mapState, mapMutations } from "vuex";

const authorityName = ["超级管理员", "管理员", "普通用户"];
export default {
  name: "CharaDialog",
  data() {
    return {
      //权限表单对话框名
      formTitleChara: "修改用户权限",

      ruleForm: {
        name: "",
        username: "",
        authority: "",
      },
      rules: {
        authority: [
          { required: true, message: "请选择权限", trigger: "change" },
        ],
      },
    };
  },
  computed: {
    ...mapState("mySystem", ["dialogVisibleChara"]),
  },
  mounted() {
    this.pubId = pubsub.subscribe("setCharaForm", (__, id) => {
      console.log("回填表单开始，id为", id);
      this.setForm(id);
    });
  },
  beforeDestroy(){
    pubsub.unsubscribe("setCharaForm");
  },
  methods: {
    ...mapMutations("mySystem", ["CHANGE_DV_CHARA"]),

    /*清空对话框数据*/
    clearForm() {
      this.ruleForm = {
        name: "",
        username: "",
        authority: "",
      };
    },

    /*更新表格时需要回调数据*/
    setForm(id) {
      //console.log(id)
      this.id = id;
      myAxios({
        method: "get",
        url: `/api/users/chara/${id}`,
      }).then((resp) => {
        console.log(resp.data);
        if (resp.data.code == "20041") {
          resp.data.data.authority = authorityName[resp.data.data.authority];
          this.ruleForm = resp.data.data;
          //修改对话框显示属性
          this.CHANGE_DV_CHARA();
        } else {
          this.$message({
            message: `${resp.data.msg}`,
            type: "error",
          });
        }
      });
    },

    /*提交表单*/
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          /*更新用户权限*/
          myAxios({
            method: "post",
            url: "/api/users/chara/update",
            data: this.ruleForm,
          }).then((resp) => {
            console.log(resp.data);
            if (resp.data.code == "20031") {
              this.CHANGE_DV(); //关闭对话框
              this.clearForm(); //清空对话框数据
              pubsub.publish("updateCharaTable");
              this.$message({
                message: "更新成功",
                type: "success",
              });
              /*刷新数据*/
            } else {
              this.$message({
                message: `${resp.data.msg}`,
                type: "error",
              });
            }
          });
        }
      });
    },

    /*点击取消，关闭表单和对话框*/
    closeForm() {
      this.CHANGE_DV_CHARA(); //关闭对话框
      this.clearForm(); //清空对话框数据
    },

    /*清空表格*/
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
  },
};
</script>

